import { Blockchain, Env } from '@haechi-labs/face-types';

import { Auth } from './auth';
import { Internal } from './internal';
import { Provider } from './provider';

export enum Network {
  'ETHEREUM',
  'ROPSTEN',
}

export class Face {
  private readonly internal: Internal;
  public auth: Auth;

  constructor(
    network: Network,
    blockchain: Blockchain,
    apiKey: string,
    iframeUrl?: string,
    apiUrl?: string,
    env?: Env
  ) {
    this.internal = new Internal(
      network,
      blockchain,
      apiKey,
      env ?? Env.ProdMainnet,
      iframeUrl,
      apiUrl
    );
    this.auth = new Auth(this.internal);
  }

  async ready(): Promise<void> {
    return this.internal.ready();
  }

  getEthLikeProvider(): Provider {
    return new Provider(this.internal);
  }

  getAddresses = async (): Promise<string[]> => {
    return await this.internal.getAddresses();
  };

  private async getUser(): Promise<void> {}
}
