import {
  Blockchain,
  Env,
  FaceLoginResponse,
  FaceMessageType,
  FaceTransactionPayload,
  JsonRpcError,
  JsonRpcRequestPayload,
  JsonRpcResponsePayload,
} from '@haechi-labs/face-types';
import { BigNumber } from 'ethers';

import { Network } from './face';
import { Iframe } from './iframe';
import { RequestArguments } from './provider';

export class Internal {
  private readonly iframe: Iframe;

  constructor(
    network: Network,
    blockchain: Blockchain,
    apiKey: string,
    env: Env,
    iframeUrl?: string,
    apiUrl?: string
  ) {
    this.iframe = new Iframe(network, blockchain, env, apiKey, iframeUrl, apiUrl);
  }

  async getAddresses(): Promise<string[]> {
    await this.iframe.sendChildMessage({
      type: FaceMessageType.FACE_ACCOUNTS,
    });
    const response = await this.iframe.waitForResponse<null | string[]>([
      FaceMessageType.FACE_ACCOUNTS,
    ]);
    if (response) {
      return response;
    }
    return Promise.reject(new Error(`get address failed`));
  }

  async estimateGas(request: RequestArguments) {
    await Promise.all(
      request.params.map(async (param: { from: string; value: string }) => {
        if (param.from) {
          const balance = await this.getBalance(param.from);
          if (balance.sub(param.value).lt(0)) {
            param.value = '0x0';
          }
        }
      })
    );
    return await this.sendRpc(request);
  }

  async getBalance(address: string): Promise<BigNumber> {
    return BigNumber.from(
      await this.sendRpc({
        method: 'eth_getBalance',
        params: [address, 'latest'],
      })
    );
  }

  async signOut(): Promise<void> {
    await this.iframe.sendChildMessage({
      type: FaceMessageType.FACE_SIGN_OUT,
    });
    const result = await this.iframe.waitForResponse<FaceLoginResponse | null>([
      FaceMessageType.FACE_SIGN_OUT,
    ]);
  }

  async ready(): Promise<void> {
    return this.iframe.ready();
  }

  async loginWithCredential(): Promise<FaceLoginResponse | null> {
    await this.iframe.showOverlay();
    await this.iframe.sendChildMessage({
      type: FaceMessageType.FACE_LOG_IN_SIGN_UP,
    });
    const result = await this.iframe.waitForResponse<FaceLoginResponse | null>([
      FaceMessageType.FACE_LOG_IN_SIGN_UP,
      FaceMessageType.FACE_CLOSE_IFRAME,
    ]);
    await this.iframe.hideOverlay();

    return result;
  }

  async sendTransaction(payload: FaceTransactionPayload): Promise<string> {
    await this.iframe.showOverlay();
    await this.iframe.sendChildMessage({
      type: FaceMessageType.FACE_SEND_TRANSACTION,
      transactionPayload: payload,
    });
    const response = await this.iframe.waitForResponse<null | Partial<
      JsonRpcResponsePayload<string>
    >>([FaceMessageType.FACE_SEND_TRANSACTION]);
    if (response && response.result) {
      return response.result;
    }
    return Promise.reject(new Error(`rpc call failed, ${response?.error?.message ?? ''}`));
  }

  async sendRpc<T>(rpcPayload: JsonRpcRequestPayload | JsonRpcRequestPayload[]): Promise<T> {
    const timestamp = await this.iframe.sendChildMessage({
      type: FaceMessageType.FACE_RPC,
      rpcPayload,
    });
    try {
      // TODO: waitForResponse timestamp
      const response = await this.iframe.waitForResponse<null | Partial<JsonRpcResponsePayload<T>>>(
        [FaceMessageType.FACE_RPC],
        timestamp
      );
      if (response && response.result) {
        return response.result;
      }
      return Promise.reject(new Error(`rpc call failed, ${response?.error?.message ?? ''}`));
    } catch (e: any) {
      return Promise.reject(new Error(`rpc call failed, ${e.message}`));
    }
  }
}
